export default {
  data() {
    return {
      page: 1,
      userInfo: "",
      joinCourseList: [],
      allCourseList: [],
      courseList: [],
      isActive: 1,
      show: false,
      item: ""
    };
  },
  /* 触底判断加载下一页 */
  onReachBottom(options) {
    let t = this,
      allCourseList = t.allCourseList;
    if (t.isActive == 3) {
      /* 判断是否加载下一页 */
      if (allCourseList.length < 5) {
        return;
      } else if (allCourseList.length >= 5) {
        t.page++;
        t.getAllCourseList();
      }
    }
  },
  onShow() {
    let t = this;
    t.getCourseList();
    t.allCourseList = [];
    t.getAllCourseList();
    t.getJoinCourseList();
  },

  onLoad() {
    let t = this;
    t.userInfo = uni.getStorageSync("userInfo");
    console.log(t.userInfo, "userInfo");
  },
  methods: {
    getItem(item) {
      let t = this;
      t.show = true;
      t.item = item;
      console.log(item, "item");
    },
    /* 加入课程 */
    joinCourse() {
      let t = this,
        userInfo = t.userInfo,
        item = t.item;
      let data = {
        user_id: userInfo.id,
        creater_id: item.user_id,
        course_id: item.id,
        courseName: item.courseName,
        className: item.className,
        creater: item.creater
      };
      t.$utils.ajax(t.$api.joinCourse, "post", data, res => {
        console.log(res);
        t.show = false;
        t.$utils.showToast(res.msg);
        t.getJoinCourseList();
      });
    },
    /* 获取已加入课程列表 */
    getJoinCourseList() {
      let t = this,
        userInfo = t.userInfo;
      let data = {
        user_id: userInfo.id
      };
      t.$utils.ajax(t.$api.getJoinCourseList, "get", data, res => {
        t.joinCourseList = res;
      });
    },
    /* 获取教课列表 */
    getCourseList() {
      let t = this,
        user_id = uni.getStorageSync("userInfo").id;
      let data = {
        user_id: user_id
      };
      t.$utils.ajax(t.$api.getCourseList, "get", data, res => {
        console.log(res, "教课列表");
        t.courseList = res;
      });
    },
    /* 获取全部课程列表 */
    getAllCourseList() {
      let t = this,
        list = t.allCourseList,
        page = t.page;
      let data = {
        page: page,
        limit: 5
      };
      t.$utils.ajax(t.$api.getCourseList, "get", data, res => {
        /* 数组拼接 */
        list = list.concat(res);
        console.log(list, "全部课程列表");
        if (list.length == 0 && page == 1) {
          console.log(111);
        }
        if (res.length == 0 && page > 1) {
          uni.showToast({
            title: "没有更多了",
            icon: "none",
            duration: 2000,
            success: () => {
              t.page--;
            }
          });
        } else {
        }
        t.allCourseList = list;
      });
    },
    toCreateClass() {
      let t = this;
      uni.navigateTo({
        url: "./createClass"
      });
    }
  }
};
